.button{
    width:60%;
    padding: 20px 35px;
    border-radius: 10%;
    margin:  10px;
}

.buttonNoHover {
    width: 60%;
    padding: 20px 35px;
    border-radius: 10%;
    margin: 10px;
}

.button:hover {
background-color: darkorange;
color: white;
}

.select {
    background-color: rgb(7, 207, 0);
    color: white;
    box-shadow: 0 0 1px black;
}

.wrong {
    background-color: rgb(233, 0, 0);
    color: white;
    box-shadow: 0 0 1px black;
}

.nextQuestion{
    padding: 15px 40px;
    border-radius: 25%;
    margin-left: 10px;
}

.nextQuestion:hover{
    background-color: aqua;
    color: azure;
}

.nopointer{
    pointer-events: none;
    color: inherit;
        text-decoration: inherit;
}